*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  background-color: #16202A;
  color: #fff;
}
.App{
  .thrends{
    
    h1{
      padding: 1em 0;
      text-align: center;
    }
    .addthrends{
      margin: 2em 0;
      text-align: center;
      a{
        text-decoration: none;
        background-color: #16202A;
        border: 3px solid #8799A5;
        color:#8799A5 ;
        font-size: 0.8em;
        padding: 0.5em;
        border-radius: 10px;
        &:hover{
          background-color: #8799A5;
          color:#16202A;
        }
      }
    }
    .thrend_container{
      margin: 0.5em 0;
      border-bottom: 1px solid #8799A5;
      padding: 1em;
      .fa-times-circle{
        background-color:#16202A  ;
        color:#8799A5;
       border-radius: 20px;
        font-size: 25px;
        position: absolute;
        right: 25px;
      }
      .thrend_title{
        font-weight: bolder;
        margin: 0.5em 0;
        text-transform: uppercase;
      }
      .thrend_content{
        font-size: 0.9em;
        color: #8799A5;
      }
    }
  }
  form{
    padding: 3em 1em;
    width: 100%;
    margin: 0;
   input{
      width: 100%;
      margin: 0.3em 0;
      padding: 0.3em;
      font-size: 1.1em;
      outline: none;
      border: none;
      background-color: #8799A5;
      color: #fff;   
    }
    .content_input{
      height: 200px;
      width: 100%;
      outline: none;
      border: none;
      padding: 0.3em;
      margin: 0.5em 0;
      background-color: #8799A5;
      color: #fff;
      padding: 0.3em;
      font-size: 1.1em;
    }
    input[type=submit]{
      width: initial;
      margin: 0 auto;
      border-radius: 10px;
      background-color: #16202A;
      border: 3px solid #8799A5;
      color:#8799A5 ;
      font-size: 0.8em;
      &:hover{
        background-color: #8799A5;
        color:#16202A;
      }
    }
  }
}